// import glanceSlideNavTabBar from '@/components/glance-SlideNavTabBar.vue';
import WucTab from '@/components/wuc-tab/wuc-tab.vue';
let id = '';
var ctime = parseInt(Date.now());
const total = 10;
export default {
	components: {
		// glanceSlideNavTabBar,
		WucTab
	},
	data() {
		return {
			swiperHeight: 0,
			stickyHeight: 0,
			isShowRecommend: false,
			isFixed: false,
			recommendList: [],
			tabIndex: 0,
			info: {},
			tabs: [{
					data: [],
					offset: 0,
					loadingType: 0
				},
				{
					data: [],
					offset: 0,
					loadingType: 0
				},
				{
					data: [],
					offset: 0,
					loadingType: 0
				},
				{
					data: [],
					offset: 0,
					loadingType: 0
				}
			],
			loadingText: {
				contentdown: '',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			stickyTop: 0,
			title: ""
		};
	},
	computed: {
		recommendBoxWidth() {
			return uni.upx2px((this.recommendList.length + 1) * 270) + 'px';
		},
		desc() {
			if (this.info.weixinOuthIntro) {
				return this.info.weixinOuthIntro;
			} else {
				if (this.info.toutiaoOuthIntro) {
					return this.info.toutiaoOuthIntro;
				} else {
					if (this.info.douyinOuthIntro) {
						return this.info.douyinOuthIntro;
					} else {
						if (this.info.appOuthIntro) {
							return this.info.appOuthIntro;
						} else {
							return '没有留下任何简介';
						}
					}
				}
			}
		},
		approve() {
			let val = '';
			if (this.info.weixinOauthStatus == '2') {
				val += '母音知名领域微信公众号认证；';
			} else {
				if (this.info.toutiaoOauthStatus == '2') {
					val += '今日头条认证；';
				} else {
					if (this.info.douyinOauthStatus == '2') {
						val += '抖音认证；';
					} else {
						if (this.info.appOauthStatus == '2') {
							val += '宝宝贝认证；';
						}
					}
				}
			}
			return val ? val : '暂无认证';
		}
	},

	onLoad(options) {
		id = options.id;
		this.init();
	},
	onShow() {
		if (uni.getStorageSync('articleIndex').toString()) { //监听文章数据改变
			let index = parseInt(uni.getStorageSync('articleIndex'))
			let articleId = this.tabs[this.tabIndex].data[index].articleId
			if (articleId.toString()) {
				this.api.home.article.get_detail({
					article_id: articleId,
					request_type: "h5"
				}, res => {
					console.log(res.data)
					this.tabs[this.tabIndex].data[index].clickNum = res.data.clickNum
					this.tabs[this.tabIndex].data[index].commentNum = res.data.commentNum
					this.tabs[this.tabIndex].data[index].praiseNum = res.data.praiseNum
					this.tabs[this.tabIndex].data[index].forwardNum = res.data.forwardNum
					uni.removeStorageSync('articleIndex')
					this.$forceUpdate()
				})
			}
		}
	},
	onPullDownRefresh() {
		this.init();
	},
	watch: {
		isShowRecommend(val) {
			setTimeout(() => {
				this.getStickyTop()
			}, 600)
		}

	},
	methods: {
		init() {
			uni.showLoading({
				title: '加载中'
			});
			this.api.home.hotVip.get_info({
					vid: id
				},
				res => {
					console.log(res);
					this.info = res.data;
					this.title = res.data.name
					uni.setNavigationBarTitle({
						title: res.data.name
					});

				}
			);
			this.getArticle();
			this.getRecommend();
			this.getStickyTop()
		},
		previewImage(url) {
			let arr = []
			arr.push(url)
			uni.previewImage({
				urls: arr
			})
		},
		async getStickyTop() {
			let size = await this.getElSize('sticky')
			this.swiperHeight = this.screenHeight - size.height;
			this.stickyHeight = size.height;
			this.stickyTop = size.top
			console.log(this.stickyTop)
		},
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true,
					rect: true
				}, (data) => {
					res(data);
				}).exec();
			})
		},
		getRecommend() {
			this.api.home.hotVip.get_recommend_list({
					id,
					type: 1
				},
				res => {
					console.log(res);
					this.recommendList = res.data
					// this.recommendList = res.data.map(el => {
					// 	let approve = '';
					// 	if (el.weixinOauthStatus == '2') {
					// 		approve = '母婴领域知名微信公众号';
					// 		el.approve = approve;
					// 		return el;
					// 	} else {
					// 		if (el.toutiaoOauthStatus == '2') {
					// 			approve = '今日头条APP知名作者';
					// 			el.approve = approve;
					// 			return el;
					// 		} else {
					// 			if (el.douyinOauthStatus == '2') {
					// 				approve = '知名母婴抖音号';
					// 				el.approve = approve;
					// 				return el;
					// 			} else {
					// 				if (el.appOauthStatus == '2') {
					// 					approve = '宝宝贝APP特邀作者';
					// 					el.approve = approve;
					// 					return el;
					// 				}
					// 			}
					// 		}
					// 	}
					// });
				}
			);
		},
		getArticle() {
			this.api.home.hotVip.get_article({
					vid: id,
					type: this.tabIndex,
					ctime,
					offset: this.tabs[this.tabIndex].offset,
					total
				},
				res => {
					console.log(res);
					uni.hideLoading();
					this.tabs[this.tabIndex].data = res.data;
				}
			);
		},
		toggleFollowed(el, type, index) {
			this.api.home.hotVip.toggle_followed({
					vid: el.userId,
					action: el.isFollowed ? 0 : 1
				},
				res => {
					console.log(res);
					if (type == 1) {
						this.info.isFollowed = !this.info.isFollowed;
					} else {
						this.recommendList[index].isFollowed = !this.recommendList[index].isFollowed;
					}
					if (el.isFollowed == 1) {
						uni.showToast({
							title: "关注成功"
						})
					} else {
						uni.showToast({
							title: "取消关注成功"
						})
					}
				}
			);
		},
		showRecommend() {
			this.isShowRecommend = !this.isShowRecommend;
		},
		clickitem(index, val) {
			this.tabIndex = index;
			this.changeTab(index);
		},
		changeSwiper(e) {
			this.tabIndex = e.target.current;
			this.changeTab(e.target.current);
		},
		getMoreArticle() {
			console.log('111');
			this.tabs[this.tabIndex].offset += total;
			this.api.home.hotVip.get_article({
					vid: id,
					type: this.tabIndex,
					ctime,
					offset: this.tabs[this.tabIndex].offset,
					total
				},
				res => {
					console.log(res);
					if (res.data.length) {
						this.tabs[this.tabIndex].data = this.tabs[this.tabIndex].data.concat(res.data);
						this.tabs[this.tabIndex].loadingType = 0;
					} else {
						this.tabs[this.tabIndex].loadingType = 2;
					}
				},
				err => {
					this.tabs[this.tabIndex].offset -= total;
					this.tabs[this.tabIndex].loadingType = 0;
				}
			);
		},
		async changeTab(index) {
			if (!this.tabs[this.tabIndex].data.length) {
				await this.init();
			}
		},
		loadMore() {
			if (!this.tabs[this.tabIndex].data.length) {
				return;
			}
			if (this.tabs[this.tabIndex].loadingType !== 0) {
				return;
			} else {
				this.tabs[this.tabIndex].loadingType = 1;
				this.getMoreArticle();
			}
		},
		showOperate(e) {
			// console.log(e);
			// uni.getSystemInfo({
			// 	success: res => {
			// 		console.log(res.windowHeight);
			// 		if (e.detail.y + 220 > res.windowHeight) {
			// 			this.articleOffsetTop = e.detail.y - 210;
			// 		} else {
			// 			this.articleOffsetTop = e.detail.y + 20;
			// 		}
			// 		this.showArticleOperate = true;
			// 	}
			// });
		},
		hideArticleOperate() {
			this.showArticleOperate = false;
		},
		removeItem(i) {
			this.recommendList.splice(i, 1);
		},
		goOther(el) {
			uni.navigateTo({
				url: '/pages/home/celebrity/detail/detail?id=' + el.userId
			});
		},
	},
	// onReachBottom() {
	// 	this.loadMore()
	// },
	onPageScroll(e) {
		// console.log(this.stickyTop+'---'+e.scrollTop)
		if (this.stickyTop >= e.scrollTop) {
			this.isFixed = false
		} else {
			this.isFixed = true
		}
	}
};
